/* src/components/Footer.css */
.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #282c34;
  color: white;
  text-align: center;
  padding: 0.5rem 0;
  opacity: 0.9; /* Slight transparency */
  transition: opacity 0.3s ease-in-out;
}

.footer:hover {
  opacity: 1; /* Fully visible on hover */
}

.content-wrap {
  padding-bottom: 2.5rem; /* Space for the floating footer */
}

